import React from 'react';
import L from 'leaflet';

import {fromFeature} from 'field-of-view';
import turfDistance from 'turf-distance';
import turfBearing from 'turf-bearing';

var cameraSvg = require('../../images/camera.svg');

import Map from '../map';

const Step = React.createClass({
  getInitialState: function() {
    return {
      hasMoved: false,
      initialView: {
        zoom: this.props.stepData.location.data.zoom + 2,
        center: [
          this.props.stepData.location.geometry.coordinates[1] + 0.0001,
          this.props.stepData.location.geometry.coordinates[0] + 0.0001
        ]
      }
    };
  },

  mapMoving: false,
  mapMovingLatLng: null,

  render: function() {

    var mapEvents = {
      move: this.onMove,
      movestart: this.onMoveStart,
      moveend: this.onMoveEnd
    };

    var options = this.state.initialView;

    return (
      <div className='geotagger-step opaque all-margin-top'>
        <h1>Direction</h1>
        <div>
          Can you
        </div>
        <div>
          <Map ref='map' options={options} defaults={this.props.defaults} mapCreated={this.mapCreated} crosshair={false} />
        </div>
        <div className='margin-top buttons'>
          <button className='button-red' onClick={this.props.abort}>Impossible</button>
          <button className='button-green' disabled={!this.state.hasMoved} onClick={this.done}>Yes! Go!</button>
        </div>
        <div className='centered'>
          <a href='javascript:;' onClick={this.props.reset}>No, I&#39;d rather geotag another image!</a>
        </div>
      </div>
    );
  },

  getFieldOfView: function(origin, target) {
    var originFeature = {
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: origin
      }
    }

    var targetFeature = {
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: target
      }
    };

    var distance = turfDistance(originFeature, targetFeature) * 1000;
    var bearing = turfBearing(originFeature, targetFeature);

    return fromFeature({
      type: 'Feature',
      properties: {
        distance: distance,
        angle: 30,
        bearing: bearing
      },
      geometry: {
        type: 'Point',
        coordinates: origin
      }
    });
  },

  getPointList: function(center, point) {
    var origin = [center.lng, center.lat];
    var target = point;

    var vof = this.getFieldOfView(origin, target);

    return [
      [
        vof.geometry.geometries[1].coordinates[0][1],
        vof.geometry.geometries[1].coordinates[0][0]
      ],
      [
        vof.geometry.geometries[0].coordinates[1],
        vof.geometry.geometries[0].coordinates[0]
      ],
      [
        vof.geometry.geometries[1].coordinates[1][1],
        vof.geometry.geometries[1].coordinates[1][0]
      ]
    ];
  },

  mapCreated: function(map) {
    var pointList = this.getPointList(map.getCenter(), this.props.stepData.location.geometry.coordinates);

    // Tweede stap:
    //   Doe de hoek, angle van field-of-view is nog vast
    //     - knop terug naar vorige stap

    // Derde stap:
    //   Doe de angle van field-of-view, met sliders
    //     - knop terug naar vorige stap

    // Twee lijnen: tussen center en twee punten links en rechts van
    //   coordinaten uit map-stap
    // En polygoon tussen dezelfde punten, en polygoon
    //   heeft gradient

    var polyline = L.polyline(pointList, {color: 'red'}).addTo(map);

    var cameraIcon = L.icon({
      iconUrl: cameraSvg,
      iconSize: [38, 38],
      iconAnchor: [19, 19]
    });

    var proto_setPos = L.Marker.prototype._setPos;
    L.Marker.include({
      _setPos: function (pos) {
        proto_setPos.call(this, pos);
console.log('HONDE')
        var bearing = 30;

        this._icon.style[L.DomUtil.TRANSFORM] += ' rotate(' + bearing + 'deg)';
      }
    });

    var cameraMarker = L.marker(new L.LatLng(40.75295,-73.98241), {
      icon: cameraIcon,
      draggable: true
    }).addTo(map);



    cameraMarker.on({
      mousedown: () => {
        map.on('mousemove', e => {
          cameraMarker.setLatLng(e.latlng);

          // https://www.mapbox.com/mapbox.js/example/v1.0.0/rotating-controlling-marker/
          var bearing = 30;




          if (this.state.polyline) {
            var center = e.latlng;
            var pointList = this.getPointList(center, this.props.stepData.location.geometry.coordinates);

            // var hond = document.getElementsByClassName('VISSEN')[0];
            // hond.setAttribute('fill', 'url(#chips)')



            this.state.polyline.setLatLngs(pointList);
            // this.state.polygon.setLatLngs(polygonPointList);

          }

          cameraMarker._icon.style[L.DomUtil.TRANSFORM] += ' rotate(' + bearing + 'deg)';
        });
      }
    });

    map.on('mouseup',function(e){
      map.removeEventListener('mousemove');
    });


    // var polygonPointList = [
    //   [
    //     40.760260692426165,
    //     -73.90914916992188
    //   ],
    //   [
    //     mapCoordinates[1],
    //     mapCoordinates[0]
    //   ],
    //   [
    //     40.78989968531352,
    //     -73.96339416503906
    //   ]
    // ];
    //
    // var polygon = L.polygon(polygonPointList, {
    //   className: 'VISSEN'
    // }).addTo(map);

    this.setState({
      map: map,
      polyline: polyline,
      // polygon: polygon
      cameraMarker: cameraMarker
    });

    var svg = document.querySelector('svg.leaflet-zoom-animated');



    var defs = document.createElementNS('http://www.w3.org/2000/svg', 'defs');
    defs.setAttribute('id', 'gradient-defs');

    svg.appendChild(defs);

    var gradient = document.createElementNS('http://www.w3.org/2000/svg', 'linearGradient');
    gradient.setAttribute('id', 'chips');
    gradient.setAttribute('x1', '0%');
    gradient.setAttribute('x2', '0%');
    gradient.setAttribute('y1', '0%');
    gradient.setAttribute('y2', '100%');

    defs.appendChild(gradient);

    //stops
    var stop1 = document.createElementNS('http://www.w3.org/2000/svg', 'stop');
    stop1.setAttribute('offset', '0%');
    stop1.setAttribute("stop-color", 'red');
    gradient.appendChild(stop1);

    var stop2 = document.createElementNS('http://www.w3.org/2000/svg', 'stop');
    stop2.setAttribute('offset', '100%');
    stop2.setAttribute('stop-color', 'yellow');
    gradient.appendChild(stop2);

  },

  // getPosition: function(element) {
  //   var xPosition = 0;
  //   var yPosition = 0;
  //
  //   while(element) {
  //     xPosition += (element.offsetLeft - element.scrollLeft + element.clientLeft);
  //     yPosition += (element.offsetTop - element.scrollTop + element.clientTop);
  //     element = element.offsetParent;
  //   }
  //
  //   return {
  //     x: xPosition,
  //     y: yPosition
  //   };
  // },
  // //
  // // onMouseMove: function(e) {
  // //   if (this.mapMoving) {
  // //     var pos = this.getPosition(e.target);
  // //     var point = [e.clientX - pos.x, e.clientY - pos.y];
  // //
  // //     var map = this.refs.map.getMap();
  // //     if (map) {
  // //       var latLng = map.containerPointToLatLng(point);
  // //       this.mapMovingLatLng = latLng;
  // //     }
  // //   }
  // // },

  onMove: function(e) {


  },

  onMoveStart: function() {
    if (!this.state.hasMoved) {
      this.setState({
        hasMoved: true
      });
    }
  },

  done: function() {
    if (this.state.hasMoved) {
      // var view = this.refs.map.getView();
      // this.props.done({zoom: view.zoom}, {
      //   type: 'Point',
      //   coordinates: view.center
      // });
      this.props.done();
    }
  }

});

export default Step;



